body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-page-css * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  line-height: 1.8em;
  font-weight: 400;
  font-family: 'Bebas Neue', cursive;
}

.main-page-css .pimg1, .main-page-css .pimg2, .main-page-css .pimg3 {
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  background-attachment: fixed;
}

/* Parallax scrolling */

.main-page-css .pimg1 {
  background-image:url(/static/media/image1.609294d5.jpg);
  min-height: 600px;
  /* max-height: 600px; */
}

.main-page-css .pimg2 {
  background-image:url(/static/media/image6.cda9f0e5.jpg);
  min-height: 900px;
  /* max-height: 900px; */
}

.main-page-css .pimg3 {
  background-image:url(/static/media/image7.babf0d6b.jpg);
}

.main-page-css .section {
  text-align: center;
  padding: 10px 80px;

}

.main-page-css .section-light {
  background-color: #f4f4f4;
  color: #666;
}

.main-page-css .section-dark {
  background-color: #282e34;
  color: #ddd;
}

.main-page-css .section-dark h2 {
  font-size: 35px;
}

.main-page-css .search {
  margin: 12px;
  color: white;
  margin-left: 10px;
  padding: 9px 25px;
  background-color: rgba(231,113,125,1);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.3s ease 0s;
}

.main-page-css button:hover {
  background-color: rgba(231,113,125,0.7);
}

.main-page-css .ptext {
  position: relative;
  width: 100%;
  color: #000;
  font-size: 27px;
  letter-spacing: 8px;
  text-transform: uppercase;
  background-color: #ff000000;
}

.main-page-css .border-welcome {
  background-color: transparent;
  color: rgba(231,113,125,1);
  font-size: 90px;
  width: 100%;
  padding-top: 130px;
  position: relative;
  text-align: center;
}

.main-page-css .welcome-message {
  background-color: transparent;
  color: rgba(231,113,125,1);
  font-size: 55px;
  position: relative;
  text-align: center;
}

.main-page-css .bigmap {
  width: 80vw;
  height: 800px;
  margin-left: 10vw;
  position: relative;
  padding-bottom: 80px;
}

.main-page-css .events {
  background-color: transparent;
  color: rgba(231,113,125,1);
  font-size: 90px;
  text-align: center;
  top: 20px;
  position: relative;
}

.myMarker {
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  border-radius: 50%;
  background-color: rgba(43,225,252, 0.5);
  position: relative;
  top: -25px;
  left: -25px;
  border: 1px solid black;
}

.evMarker {
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.5);
  position: relative;
  top: -20px;
  left: -20px;
  border: 1px solid black;
}

.cards-css {
  background: none;
}

.cards-css * {
  margin: 0px;
  padding: 0px;
  font-family: 'Bebas Neue', cursive;
  background: none;
}

.cards-css .card-container {
  width: 80vw;
  position: relative;
  left: 10vw;
  /* background: #FFF; */
  display: grid;
  width: 80vw;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 36px;
  padding-top: 100px;
  padding-bottom: 200px;
}


.cards-css .card {
  /* border-radius: 6px; */
  width: 25vw;
  height: 300px;
  /* border: 5px solid #e7717d; */
  position: relative;
  /* background-color: rgba(255,255,255,1); */
  background-color: rgba(251,196,191,1);
  transition: ease 0.3s;
}

.cards-css .card:hover {
  position: relative;
  z-index: 2;
  transform: scale(1.10);
  transition: .3s;
  box-shadow: 0 0 15px 5px black;
}

.cards-css .card-image {
  object-fit: cover;
  width: 90%;
  margin-left: 5%;
  margin-top: 5%;
  max-height: 200px;
  /* border-radius: 6px; */
  /* border: 4px solid rgba(251,196,191,1); */
}

.cards-css .card .title{
  left: 5%;
  bottom: 30px;
  position: absolute;
}

.cards-css .card .likes{
  right: 5%;
  bottom: 30px;
  position: absolute;
}

.cards-css span {
  color: #666;
  font-size: 25px;
}

.login-page-css * {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', cursive;
  color: #edf0f1;
}

.login-page-css .title {
  font-size: 40px;
  text-align: center;
  padding: 8px;
  margin: 13px;
}


.login-page-css .hero {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4),rgba(0, 0, 0, 0.4)),url(/static/media/login_background.56997854.jpg);
  background-position: center;
  position: absolute;
}

.login-page-css .form-box {
  width: 380px;
  height: 480px;
  position: relative;
  margin: 10% auto;
  background-color: rgba(251,196,191,1);
  padding: 5px;
  overflow: hidden;
  box-shadow: 0 0 50px 5px #666;
  border: 3px solid #e7717d;
  border-radius: 30px;
}

.login-page-css .button-box {
  width: 220px;
  margin: 35px auto;
  position: relative;
  box-shadow: 0 0 50px 5px #edf0f1;
  border-radius: 30px;
  opacity: 0.95;
}

.login-page-css ::-webkit-input-placeholder {
  color: #e7717d;
  font-size: 20px;
}

.login-page-css :-ms-input-placeholder {
  color: #e7717d;
  font-size: 20px;
}

.login-page-css ::-ms-input-placeholder {
  color: #e7717d;
  font-size: 20px;
}

.login-page-css ::placeholder {
  color: #e7717d;
  font-size: 20px;
}

.login-page-css .toggle-btn {
  padding: 10px 30px;
  cursor: pointer;
  background: transparent;
  border: 0;
  outline: none;
  position: relative;
  font-size: 20px;
}

.login-page-css #btn {
  top: 0;
  left: 0;
  position: absolute;
  width: 110px;
  height: 100%;
  background: linear-gradient(to right, #fbc4bf,#e7717d);
  border-radius: 30px;
  transition: .5s;
}


.login-page-css .input-group {
  top: 180px;
  position: absolute;
  width: 280px;
  transition: .5s;
}

.login-page-css .input-field {
  width: 100%;
  padding: 14px 0;
  margin: 5px 0;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #edf0f1;
  outline: none;
  background: transparent;
  font-size: 20px;
  color: #666;
}


.login-page-css .submit-btn {
  width: 85%;
  padding: 10px 30px;
  cursor: pointer;
  display: block;
  margin: auto;
  background: linear-gradient(to right, #fbc4bf,#e7717d);
  margin: 33px;
  border: 0;
  outline: none;
  border-radius: 30px;
  font-size: 20px;
  transition: ease 0.3s;
}

.login-page-css .submit-btn:hover {
  transform: scale(1.15);
  transition: .3s;
}

.login-page-css #login {
  left: 50px;
}

.login-page-css #register {
  left: 450px;
}

.newpost-css{
  position: fixed;
  top: 0;
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', cursive;
  color: #edf0f1;
  z-index: 1;
  background: rgba(0,0,0,0.8);
  height: 100vh;
  width: 100vw;
}

.newpost-css form{
  min-width: 400px;
  max-width: 30vw;
  height: 520px;
  position: relative;
  margin: 10% auto;
  background-color: rgba(251,196,191,1);
  padding: 5px;
  overflow: hidden;
  box-shadow: 0 0 25px 5px #666;
  /* border: 3px solid #e7717d; */
  /* border-radius: 30px; */
}

.newpost-css button{
  margin-bottom: 12px;
  color: white;
  padding: 9px 25px;
  background-color: rgba(231,113,125,1);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.3s ease 0s;
  font-family: 'Bebas Neue', cursive;
  width: 36%;
  position: absolute;
  bottom: 0;
  left: 32%;
}

.newpost-css h3{
  width: 100%;
  text-align: center;
  /* text-decoration: underline; */
  font-size: 60px;
  margin: 15px 0;
}

.newpost-css label{
  width: 100%;
  display: block;
  text-align: center;
  /* text-decoration: underline; */
  font-size: 30px;
  margin: 10px, 0;
  padding-bottom: 5px;
}

.newpost-css input{
  width: 60%;
  display: block;
  position: relative;
  text-align: center;
  font-size: 20px;
  font-family: 'Bebas Neue', cursive;

  left: 20%;
  margin-bottom: 8px;

}
.newpost-css textarea{
  width: 80%;
  height: 70px;
  display: block;
  position: relative;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 12px;
  left: 10%;
  margin: 0;
}

.newpost-css .closeButton{
  width: 18px;
  height: 18px;
  border: 3px solid white;
  line-height: 18px;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-weight: bolder;
  text-align: center;
  margin: 0;
  position: absolute;
  top: 18px;
  right: 18px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.newpost-css .uploadImage{
  color: white;
  padding: 9px 0px;
  margin-top: 15px;
  background-color: rgba(231,113,125,1);
  border-radius: 50px;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.3s ease 0s;
  width: 36%;
  position: relative;
  left: 32%;
  text-align: center;
}

.newpost-css .previewImg {
  position: relative;
  object-fit: contain;
  max-width: 40%;
  max-height: 91px;
  margin-top: 10px;
  left: 30%;
}

.newpost-css button:hover, .newpost-css .uploadImage{
  background-color: rgba(231,113,125,0.7);
}

.newpost-css .closeButton:hover{
  color: rgba(231,113,125,1);
  border-color: rgba(231,113,125,1);
}

